@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800&display=swap);
.footer {
    width: 100%;
    background-color: #272c39;
    min-height: 5vh;
    padding: 20px 0px 16px 0px;
    border:black
}

.footer p{
    color:#5D667E;
    text-align: center;
    font-size: 13px;

}
header {
    width: 100%;
    height: 100px;
    background-color: var(--darkgrey);
}

header .navbar {
    max-width: 1240px;
    height: 100%;
    padding: 0 20px;
    margin: auto;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    background-color: #272c39;
    border-radius: 20px;
    
}
.navbar h3{
    color:white;
    
}

header .logo {
    display: flex;
    align-items: center;
    width: 100%;
}

header .logo img {
    width:30%;
    height: auto;
}

.hamburger {
    display: none;
    position: absolute;
    top: 2rem;
    right: 1rem;
}

.nav-menu {
    display: flex;
    align-items: center;
    justify-self: start;
}

.nav-item {
    padding: 0 1.5rem;
}

.nav-link {
    color: #fff;
}
a{

text-decoration: none;
color:white;

}

@media screen and (max-width:960px) {
    .hamburger {
        display: block;
    }
    .nav-menu {
        position: fixed;
        left: -100%;
        flex-direction: column;
        justify-content: center;
        background-color: var(--darkgrey);
        width: 100%;
        height: 100%;
        text-align: center;
        transition: 0.4s;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-item {
        margin: 1.5rem 0;
        font-size: 1.8rem;
    }
}







.hero {
    width: 100%;
    height: 100vh;
    background-color: var(--darkgrey);
}

.hero .container {
    max-width: 1240px;
    padding: 0 1rem;
    margin: auto;
}

.hero .content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.hero .col-1 {
    padding: 1rem 1rem 1rem 0;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hero .col-1 h1 {
    font-size: 72px;
    font-weight: 700;
    color: #fff;
}

.hero .col-1 p {
    font-size: 0.9rem;
    color: white;
    line-height: 1.4;
    margin-top: 0;
    margin-bottom: 0.9rem;
    text-align: justify;
    
}

.hero .used-by p {
    color: var(--lightgrey);
    font-size: 12px;
    margin: 2rem 0 1rem 0;
}

.hero .col-1 .icons i {
    color: var(--grey);
    padding-right: 1rem;
}


.hero .col-2 {
    padding: 1rem;
    /* height: 70vh; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: .8rem;
}

.hero .col-2 p {
    color: var(--grey);
}

.hero .form-layout {
    width: 400px;
    margin: auto;
    background-color: #fff;
    border-radius: 6px;
}

.hero .form-container {
    padding: 1.5rem 2rem;
}

.hero .sign-in-txt {
    margin-bottom: 10px;
}

.social-login i {
    background-color: #fff;
    padding: 16px 45px 6px 45px;
    border: 1px solid var(--lightgrey);
    border-radius: 4px;
}


.col-2 .divider p {
    width: 100%;
    text-align: center;
    border-bottom: 1px solid var(--lightgrey);
    line-height: .1em;
    margin: 2rem 0 2rem;
}

.col-2 .divider p span {
    background-color: #fff;
    padding: 0 10px;
}

form input {
    display: block;
    width: 100%;
    margin: 1.2rem 0;
    padding: 8px;
    border: 1px solid var(--lightgrey);
    border-radius: 4px;
}

.hero .form-footer {
    border-top: 1px solid var(--lightgrey);
    background-color: #f8f8f8;
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
}

.form-footer p {
    padding: 1.5rem 2rem;
}

.form-footer span {
    font-weight: 500;
}

@media screen and (max-width:960px) {
    .hero {
        height: auto;
        width: 100%;
    }

    .hero .container {
        padding: 0 1rem;
    }

    .hero .content {
        grid-template-columns: 1fr;
    }

    .hero .col-1 {
        height: 90vh;
        padding: .2rem;
        max-width: 100%;
    }
    
    .hero h3 {
        font-size: 52px;
     }
    
     .hero h1 {
       font-size: 72px;
    }

    .hero .col-1 {
        display: none;
    }

    .hero .secondarytext{
        font-size: 1.5rem;
    }
    .hero .largetext{
        font-size: 6rem;
    }
    
}

