* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary: #6948ff;
  --secondary: #553bcb;
  --darkgrey: #22262a;
  --lightgrey: #d3d3d3;
  --grey: #808080;
}

body {
color: #333;
font-family: 'Montserrat', sans-serif;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  -webkit-columns: #fff;
          columns: #fff;
}

.primary-color {
  color: #6948ff;
  color: var(--primary);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  width: 100%;
  padding: 8px 0;
  border-radius: 4px;
  border: 1px solid #6948ff;
  border: 1px solid var(--primary);
  background-color: #6948ff;
  background-color: var(--primary);
  color: #fff;
  cursor: pointer;
}

button:hover {
  border: 1px solid #553bcb;
  border: 1px solid var(--secondary);
  background-color: #553bcb;
  background-color: var(--secondary);
}

.modal-content {
  position: relative;
  background-color: white;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  border-radius: 12px;
  outline: 0;
  box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
}

.modal-header {
  background-color: #1e2329;
}

.modal-header button {
  outline: 0;
}
#claimnow .modal-close {
  margin-top: 4px;
  margin-right: 15px;
}

#claimnow .modal-header,
#confirm .modal-header {
  background: transparent;
  border-bottom: 0px;
}

#confirm .modal-header {
  padding-bottom: 0px;
}

#walletList .modal-header {
  border-bottom: 0px;
}

#allowanceModal .btn-send {
  width: 100%;
  font-size: 28px;
  font-weight: bold;
}


.modalheight {
  max-height: 90vh;
  overflow: auto;
}


.container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-auto-rows: minmax(100px, auto);
  grid-gap: 10px;
}

.token {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 150px;
  height: 150px;
  overflow: hidden;
  border-radius: 20px;
  margin-top: 50px;
  transition: all 0.2s;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.3);
}

.logout {
  margin-left: 10px;
}

.jcNZzC {
  transition: background-color 0.2s ease-in-out 0s;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(255, 255, 255);
  border-radius: 12px;
  padding: 24px 16px;
}

